html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/*фон шапки*/
.header_background_color {
  background: rgba(59, 53, 40, 0.6);
  position: relative;
}
/*блок header*/
.header {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-auto-rows: auto;
  justify-content: center;
  align-content: center;
  justify-items: center;
  align-items: center;
  font-family: 'Roboto', sans-serif;
}
/*навигация*/
.header__nav {
  grid-column-start: 1;
  grid-column-end: -1;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
}
.header__list {
  display: flex;
  justify-content: space-around;
  align-content: center;
  font-weight: 700;
  text-transform: uppercase;
}
.scream {
  font-size: 1.125rem;
  color: #4f095c;
}
/*ссылки*/
.nav__links {
  display: flex;
  justify-content: space-around;
  align-content: center;
  font-weight: 700;
  text-transform: uppercase;
  width: 60%;
}
.nav__links a {
  color: #4f095c;
  margin-top: 20px;
  line-height: 24px;
}
.nav__links a:hover {
  color: #10cabf;
  border-bottom: 1px #10cabf solid;
}
/*логотип*/
.header__logo {
  margin: 10px 0;
}
/*бургер меню*/
.burger {
  margin-top: 14px;
  display: none;
  transition: all 0.3s ease;
}
.burger div {
  width: 30px;
  height: 3px;
  background-color: #5a1a4a;
  margin: 5px;
}
/*наведение на ссылки*/
.header__link-hover:hover {
  color: #10cabf;
  border-bottom: 1px #10cabf solid;
}
.burger__menu {
  margin-top: 14px;
  display: none;
  transition: all 0.3s ease;
}
.burger__menu div {
  width: 30px;
  height: 3px;
  background-color: #5a1a4a;
  margin: 5px;
}
/*первый экран*/
.first__page {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(4, auto);
  background: url(../../build/img/bg/25853939abf2b28f6e53c8bd71646e7e.png) no-repeat center center;
  position: relative;
  justify-content: center;
  align-content: center;
  justify-items: center;
  align-items: center;
  /*наложение цвета на первый экран*/
}
.first__page::before {
  position: absolute;
  background-color: rgba(1, 1, 1, 0.8);
  content: '';
  display: block;
  width: 100%;
  height: 100%;
}
/*заголовок первого экрана*/
.page__title {
  grid-column-start: 1;
  grid-column-end: -1;
  position: relative;
  display: block;
  text-align: center;
  padding-top: 100px;
}
/*блок с кнопокой*/
.header__button {
  grid-column-start: 1;
  grid-column-end: -1;
  position: relative;
  cursor: pointer;
  margin-top: 100px;
  padding: 20px 70px;
  background-color: #10cabf;
  border-radius: 10px;
  border: none;
  color: white;
  margin-bottom: 100px;
  font-size: 1.125rem;
}
/*заголовки второго уровня*/
.title {
  grid-column-start: 1;
  grid-column-end: -1;
  font-size: 2rem;
  margin: 70px 0px 40px 30px;
}
/*слайдер*/
.card {
  grid-column-start: 1;
  grid-column-end: -1;
  border: 5px solid #4f095c;
  margin: 0 20px;
  padding: 25px 50px;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  position: absolute;
  opacity: 1;
  transition: all ease 1s;
}
/*картинки карточки*/
.slider__img img {
  grid-column-start: 1;
  grid-column-end: 3;
}
/*описание карточки*/
.slider__title {
  grid-column-start: 3;
  grid-column-end: -1;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}
/*стоимость товара в карточке*/
.card__price {
  grid-column-start: 1;
  grid-column-end: 2;
}
/*блок с кнопокой*/
.sale__button {
  grid-column-start: 2;
  grid-column-end: -2;
  text-align: center;
  position: relative;
  cursor: pointer;
  padding: 20px 20px;
  background-color: #10cabf;
  border-radius: 10px;
  border: none;
  color: white;
  font-size: 1.125rem;
  width: 350px;
  margin-left: 30px;
}
/* высота отдельных кнопок*/
.sale__button-height {
  height: 60px;
}
/*прозрачность карточки*/
.sale__slider .opacity0 {
  opacity: 0;
}
/*Нижний скрол*/
.scroll {
  display: inline-flex;
  justify-content: center;
  align-content: space-between;
}
.circle_scroll {
  width: 14px;
  height: 14px;
  background-color: #10cabf;
  border-radius: 100%;
  margin: 30px 30px;
}
.active-color {
  background-color: #4f095c;
}
/*секция с каталогом*/
.catalog {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(3, auto);
  color: white;
}
/*бекграунд каталога*/
