/*расположение небольших картинок*/
.pieces__img {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  position: absolute;
  left: 27%;
  top: 30%;
}
.pieces__img-marginTop {
  top: 30%;
}
/*размер небольших картинок*/
.pieces__img-size {
  height: 100px;
  width: 100px;
  margin-bottom: 30px;
}
/* располоение цветовой гаммы*/
.color__img {
  display: flex;
  padding-top: 20px;
}
/*расстояние между цветовыми гаммами*/
.right__pieces {
  margin-left: 20px;
}
/*отступ с картинками цветовой гаммы*/
.color__img-padding {
  padding: 5px;
}
/*основное описание двери*/
.text__img {
  margin-left: 50px;
  line-height: 1.5rem;
  font-size: 1.125rem;
}
.sale__door {
  margin-top: 20px;
}
.sale__door-size {
  font-size: 1.5rem;
  color: #10cabf;
  margin-top: 10px;
}
/*контейнер со спецификацией*/
.table__door {
  margin: 10px 20px;
  color: #5a1a4a;
  font-family: Philosopher, sans-serif;
  margin-bottom: 40px;
  margin-left: 40px;
}
/*размеры таблицы*/
.door__table {
  width: 100%;
  height: 200px;
}
/*противопожарные двери*/
.about__noFire {
  grid-template-columns: 300px auto;
  display: grid;
  column-gap: 40px;
  margin: 10px 20px;
  margin-left: 40px;
  color: #5a1a4a;
  border-bottom: 2px solid #5a1a4a;
  padding-bottom: 40px;
  font-family: Philosopher, sans-serif;
}
.noFire__door {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-auto-rows: 200px;
}
.left__list {
  grid-column-start: 1;
  grid-column-end: 2;
}
.right__list {
  grid-column-start: 2;
  grid-column-end: -1;
}
.piecesImg {
  grid-column-start: 1;
  grid-column-end: -1;
  margin-top: 80px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-right: 20%;
}

/*основные стили*/
* {
